.App-component {
  border: 1px solid;
  margin: 10px;
  margin-bottom: 50px;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 26px;
}

.App-content {
  display: flex;
  justify-content: center;
  padding: 20px;
  width: 50%;
  margin: 0 auto;
  background-color: white;
}

.App-login {
  background-color: #45c337;
}
